.form {
    display: block;
    width: 300px;
    height: 50px;
    margin: 0 auto 20px;
}
.inputName,
.inputNumber {
    width: 300px;
    height: 40px;
    margin-bottom: 10px;
    font-size: 25px;
    text-align: center;
    outline: none;
    border: none;
    border-radius: 25px;
}
.buttonAddContacts {
    width: 300px;
    height: 40px;
    outline: none;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    font-size: 25px;
    margin: 15px auto;
    background-color: wheat;
}
.buttonAddContacts:hover,
.buttonAddContacts:focus {
    background-color: rgb(92, 194, 92);
}